
.root {
  position: relative;

  }

.input {
  border: 1px solid rgba(0,0,0, 0.1);
  padding: 12px 20px 12px 42px;
  border-radius: 10px;
  font-size: 16px;
  max-width: 200px;

  @media (max-width: 1100px) {
    max-width: 130px;
  }

  @media (max-width: 700px) {
    max-width: 300px;
    align-items: center;
  }

  &:focus {
    border: 1px solid rgba(0,0,0, 0.25);
  }
}

.icon {
  width: 22px;
  height: 22px;
  position: absolute;
  opacity: 0.3;
  left: 14px;
  top: 12px
}

.clearIcon {
  width: 22px;
  height: 22px;
  position: absolute;
  opacity: 0.3;
  top: 12px;
  right: 12px;
  cursor: pointer;
  &:hover {
    opacity: 0.8;

  }
}
