@import '../variables';

.header {
  border-bottom: 1px solid $gray-line;
  padding: 70px 20px;

 .info{
    margin-bottom: 20px;
   margin-top: -50px;
   display: flex;
   justify-content: space-around;



   @media ( max-height: 900px) {
     font-size: 12px;
     display: grid;
     grid-auto-columns: 1fr 1fr 1fr 1fr;
     grid-auto-rows: 1fr 1fr;
     grid-template-columns: 1fr;
     grid-template-rows: 1fr 1fr 1fr;
         grid-template-areas:
             "."
             "."
             ".";
   }
}

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media (max-width: 1100px) {
      display: grid;
      grid-auto-columns: 1fr 1fr 1fr 1fr;
      grid-auto-rows: 1fr 1fr;
      grid-template-columns: 1fr 1fr;
      grid-template-rows: 1fr 1fr;
      gap: 20px 19px;
      grid-template-areas:
              ". ."
              ". .";
      margin-bottom: -70px;

    }
    @media (max-width: 700px) {
      display: grid;
      grid-auto-columns: 1fr 1fr 1fr 1fr;
      grid-auto-rows: 1fr 1fr;
      grid-template-columns: 1fr;
      grid-template-rows: 1fr 1fr 1fr 1fr;
      gap: 20px 19px;
      grid-template-areas:
              "."
              "."
              "."
              ".";
      margin-bottom: -150px;
    }

    .active1 {
      background-color: #282828;
      color: #fff;
      margin-bottom: 20px;
      padding: 13px 30px;
      border-radius: 30px;
      margin-right: 10px;
      font-weight: bold;
      cursor: pointer;
      transition: background-color 0.1s ease-in-out;

      @media (max-width: 700px) {
        padding: 8px 15px;
        margin-right: 5px;
      }
    }
  }

  &__logo {
    display: flex;
    margin-top: 20px;

    img {
      margin-right: 15px;
    }

    h1 {
      color: #181818;
      font-size: 24px;
      letter-spacing: 1%;
      text-transform: uppercase;
      font-weight: 800;
    }

    p {
      color: #7b7b7b;
    }
  }
}
