@import '../variables';

.pizza-block {
  width: 300px;
  text-align: center;
  height: 350px;

  &-wrapper {
    display: flex;
    justify-content: center;
    background-color: rgb(243, 236, 236);
    border-radius: 20px ;
    margin-bottom: 50px;
  }

  &__image {
    width: 300px;
    border-radius: 20px 20px 0 0 ;

  }

  &__title {
    font-size: 16px;
    font-weight: 900;
    letter-spacing: 1%;
    margin-top: 10px;

  }

  &__selector {


    ul {
      display: flex;
      flex: 1;


      &:first-of-type {
        margin-bottom: 6px;
      }

      li {
        padding: 8px;
        flex: 1;
        cursor: pointer;
        font-weight: 600;
        font-size: 14px;

        @include noselect();
        &.active {
          background-color: #e0e0e0;
          box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.04);
          border-radius: 5px;
          cursor: auto;
        }
      }
    }
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 20px 20px 0 20px;
  }

  &__price {
    font-weight: bold;
    font-size: 22px;
    line-height: 27px;
    letter-spacing: 0.015em;
  }
}
